.card {
  width: 100%;
  margin-bottom: 30px;
}

.heading {
  font-weight: bold;
  margin-bottom: 10px;
  color: #5624d0;
  text-decoration: underline;
}

.job {
  color: #777d81;
  margin-bottom: 20px;
}

.instructor {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
}

.image {
  border-radius: 50%;
}

.skills {
  display: flex;
  gap: 10px;
  align-items: flex-start;
  font-size: 90%;
}

.icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.data {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 10px;
}

.words {
  white-space: nowrap;
}

.hide {
  height: 180px;
  overflow: hidden;
  text-overflow: ellipsis;
  position: relative;
}

.hide:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  background: linear-gradient(transparent 150px, white);
}

.button {
  background-color: transparent;
  border: transparent;
  color: #5624d0;
  font-weight: bold;
}

.button:hover {
  color: #401b9c;
}
